/* Example:
h1 {
  font-size: rem(30);  <-- this is 1.875rem
}
*/
.header {
  background-color: #BDD4DA;
  color: #014038;
  padding: 15px 0;
  margin-top: 1.5rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 54.375em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 54.3125em) {
  .header {
    padding: 0.625rem 0;
  }
}
.header__hamburger {
  display: flex;
  align-items: center;
  margin-left: 0.625rem;
}
.header__logo {
  display: flex;
  align-items: center;
  margin: 0 auto;
}
.header__search, .header__searchbar, .header__shoppingcart, .header__account {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: auto;
  margin-right: 0.625rem;
}
.header__search {
  margin-right: 0.125rem;
}
.header input[type=text] {
  padding-left: 0.625rem;
  box-sizing: border-box;
  border: 0 solid var(--sky-bloom);
  height: 1.875rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--meadow-mist);
}
@media (max-width: 54.3125em) {
  .header input[type=text],
  .header .header__account {
    display: none;
  }
}
.header__left {
  display: flex;
  justify-content: flex-start;
  flex: 1;
}
.header__center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0.625rem;
  margin: 0 auto;
  text-align: center;
  position: center;
}
.header__right {
  display: flex;
  justify-content: flex-end;
  flex: 1;
}